* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f8f9fa;
  color: #333;
}

#app {
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  padding: 20px;
  width: 340px;
  text-align: center;
}

h2 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #495057;
}

h3 {
  font-size: 15px;
  color: #6c757d;
  margin-bottom: 5px;
}

/* Break and Session Controls */
.break-session-length {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 15px;
}

.break-session-length div {
  text-align: center;
}

button {
  background-color: #495057;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  cursor: pointer;
  transition: background 0.3s;
  width: 80px; /* Ensures all text fits comfortably */
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}

button:hover {
  background-color: #6c757d;
}

button:disabled {
  background-color: #ccc;
}

/* Break and Session Length */
#break-length, #session-length {
  display: block;
  font-size: 16px;
  color: #495057;
  margin: 5px 0;
}

#break-increment, #break-decrement,
#session-increment, #session-decrement {
  display: block;
  margin: 5px auto;
}

/* Timer Section */
.timer {
  margin-top: 20px;
  padding: 15px;
  background-color: #f0f4f7;
  border-radius: 8px;
}

#timer-label {
  font-size: 16px;
  margin-bottom: 4px;
  color: #495057;
}

#time-left {
  font-size: 28px;
  font-weight: bold;
  color: #e76f51;
  margin: 8px 0;
}

/* Button Layout - Start/Stop & Reset */
.button-container {
  display: flex;
  justify-content: center;
  gap: 10px; /* Ensures space between the buttons */
  margin-top: 10px;
}

#start_stop, #reset {
  margin: 8px 4px;
  padding: 8px 16px;
  font-size: 14px;
  border-radius: 4px;
  width: 120px; /* Adjust width if needed */
}

#reset {
  background-color: #e76f51;
}

#reset:hover {
  background-color: #d34a34;
}

/* Button and time display animations */
button, #time-left {
  transition: all 0.3s ease-in-out;
}

button:active {
  transform: scale(0.95);
}
